<template>

	<div class="RFOrderShip" ref="CurrentVue">
		<header><i class="iconfont icon-xiangzuo1" @click="toIndex"></i><span>发运</span><i class="iconfont icon-saoma" @click="scan"></i></header>

		<div class="container">

			<div>
				<span>发运单号</span>
				<input class="writable"  @focus="g_focus('OrderKey')" @blur="g_blur()"  @change="toUppercase('OrderKey')"   ref="OrderKey" placeholder="请输入内容" v-model="OrderKey" @keyup.enter="RF_OrderQuery(true, false)"></input>
				<i v-if="OrderKey" @click="clearInput('OrderKey')" class="iconfont icon-cuowu"></i>
			</div>

			<div>
				<span>预期日期</span>
				<input v-model="RequestedShipDate" readonly></input>
			</div>

			<div>
				<span>收货方</span>
				<input v-model="C_company" readonly></input>
			</div>


			<div class="address">
				<span>地址</span>
				<textarea v-model="C_address1" readonly></textarea>
			</div>

			<div>
				<span>总数量</span>
				<input v-model="QtyTotal" readonly></input>
			</div>

			<div>
				<span>总体积</span>
				<input v-model="TotalVol" readonly></input>
			</div>

			<div>
				<span>总重量</span>
				<input v-model="TotalWgt" readonly></input>
			</div>

			<div class="g_doubleBtn">
				<div ref="RF_Btn_OrderShip" @click="RF_OrderShip">发运确认</div>
				<div @click="RF_OrderDetailQuery">查看明细</div>
				<!-- 辅助光标不能在 发运确认 上的问题，该控件不显示 -->
				<input ref="fuzhu" class="fuzhu" @keyup.enter="ConfirmShip" ></input>
			</div>

		</div>

		<div v-if="tipsHide" @touchmove.prevent class="g_tips">
			<div>
				<span>提示信息</span>
				<span>{{msg}}</span>
				<p  @ref="inputFocus" @click='tipsHideen(inputFocus)'>确定</p>
			</div>
		</div>

	</div>
</template>
<script type="text/javascript">

	export default{
		name:"RFOrderShip",
		data(){
			return{   
        OrderKey:'', // 0000003504
        C_company:'',
        C_address1:'',
        QtyTotal:'',
        TotalVol:'',
        TotalWgt:'',
        RequestedShipDate:'',
        Status:0,
        tipsHide:false,//提示信息是否显示
        inputFocus:"",
        tokenString:"",
        SkuCnt:0,
    }
},
mounted(){

	this.$refs.OrderKey.focus();
	var that = this;

	this.$refs.CurrentVue.addEventListener('touchstart', this.CurrentVueTouchstart);

	window.QRScanAjax = this.QRScanAjax;
	$("input").attr("maxlength","50");
},
created(){
	
	this.OrderKey = this.$store.state.RFOrderShip_RFOrderQuery.OrderKey;
	this.RequestedShipDate = this.$store.state.RFOrderShip_RFOrderQuery.RequestedShipDate;
	this.C_company = this.$store.state.RFOrderShip_RFOrderQuery.C_company;
	this.C_address1 = this.$store.state.RFOrderShip_RFOrderQuery.C_address1;
	this.QtyTotal = this.$store.state.RFOrderShip_RFOrderQuery.QtyTotal;
	this.TotalVol = this.$store.state.RFOrderShip_RFOrderQuery.TotalVol;
	this.TotalWgt = this.$store.state.RFOrderShip_RFOrderQuery.TotalWgt; 
},
methods:{
	// 接收iOS/android值
	QRScanAjax:function (qrcode){

		var inputName = this.$store.state.ScanQRCodeInputName;

        this[inputName] = qrcode;

        this.RF_OrderQuery(true, false);

    },
    // 屏幕点击事件
	CurrentVueTouchstart:function(){
        
		this.focusOrderShipBtn(false);
		console.log("屏幕点击事件");
	},
	// 发运确认 
	ConfirmShip:function(){

		console.log("ConfirmShip----------");

		var that = this;

        // 当光标聚焦在 "发运确认" 回车后触发接口RF_OrderShip
        if(that.$refs.RF_Btn_OrderShip.style.backgroundColor == "rgb(0, 0, 139)"){


				// 总数量为空，先执行 "发运订单查询"
				if(!that.QtyTotal) {

					that.RF_OrderQuery(true, false);
				}else {

					that.focusOrderShipBtn(false);
					that.RF_OrderShip_commit();
				}
			}
		},
	focus(e) {
		
		// event.currentTarget.select();
	},
	// 清空输入框
	clearData:function(){

		this.RequestedShipDate = "";
		this.C_company = "";
		this.C_address1 = "";
		this.QtyTotal = "";
		this.TotalVol = "";
		this.TotalWgt = "";
		this.Status = "";
		this.$store.state.RFOrderShip_RFOrderQuery={};
	},
	// 光标聚集到确认按钮
	focusOrderShipBtn:function(focus){

		console.log("fsd");

		if(this.$refs["RF_Btn_OrderShip"]){

			this.$refs["RF_Btn_OrderShip"].style.backgroundColor = focus ? "#00008B" : "#285DCE";
		}
	},
    // 查询发运订单
    // isfocuRF_Btn_OrderShip 光标是否聚焦到确认按钮
    // check_after_OrderShip 查询后是否需要立即发运
    RF_OrderQuery:function(isfocuRF_Btn_OrderShip, check_after_OrderShip){

      	console.log("isfocuRF_Btn_OrderShip: " + isfocuRF_Btn_OrderShip);
      	console.log("check_after_OrderShip: " + check_after_OrderShip);
    	var that = this;
		this.clearData();
		this.$refs.OrderKey.blur();

    	if(!this.OrderKey){

    		this.msg = "发货单号不能为空";
    		that.tipsShow("OrderKey");
    		that.inputFocus = "OrderKey";
    	}else{
    		var currentUrl = this.HOST + "RFOrderQuery.do";

      		var postData = {

      			"OrderKey":this.OrderKey,
      		}

      		this.httpRequest(currentUrl,postData,function(res){

      			if(res.data.data.length == 0){

      				that.msg = "订单待发运，发运单号:" + that.OrderKey;
      				that.tipsShow("OrderKey");
      				that.inputFocus = "OrderKey";
      			}else{

					that.$store.state.RFOrderShip_RFOrderQuery=res.data.data[0];
      				that.RequestedShipDate = res.data.data[0].RequestedShipDate;
      				that.C_company = res.data.data[0].C_company;
      				that.C_address1 = res.data.data[0].C_address1;
      				that.QtyTotal = res.data.data[0].QtyTotal;
      				that.TotalVol = res.data.data[0].TotalVol;
      				that.TotalWgt = res.data.data[0].TotalWgt;
      				that.Status = res.data.data[0].Status;
      				that.SkuCnt = res.data.data[0].SkuCnt;

      				if(that.Status < 14){

      					that.msg = "发货单未分配库存，不能发运";
      					that.tipsShow("OrderKey");
      					that.inputFocus = "OrderKey";
      				}else if(that.Status == 95){

      					that.msg = "发货单已经发运";
      					that.tipsShow("OrderKey");
      					that.inputFocus = "OrderKey";
      				}else if(that.Status > 95){

      					that.msg = "发货单已经取消，不能发运";
      					that.tipsShow("OrderKey");
      					that.inputFocus = "OrderKey";
      				}else{

      					if(check_after_OrderShip) {

      						that.RF_OrderShip_commit();
      					}else {

      						that.focusOrderShipBtn(isfocuRF_Btn_OrderShip);
      						that.$refs.fuzhu.focus();
      					}
      				}
      			}
      		},"OrderKey")
      	}
    },
    // 发运确认(第一步)
    RF_OrderShip(){

      	var that = this;

      	if(!this.OrderKey){

      		this.msg = "请输入【发货单号】";
      		that.tipsShow("OrderKey");
      		that.inputFocus = "OrderKey";
      	}else{

      		// 总数量为空，先执行 "发运订单查询"
      		if(!this.QtyTotal) {

      			this.RF_OrderQuery(true, false);
      		}else {

      			this.RF_OrderQuery(true, true);
      		}
      	}
    },
    // 发运确认(第二步)
    RF_OrderShip_commit(){

      	var that = this;
      	var currentUrl = this.HOST + "RFOrderShip.do";

      	var postData = {

      		"OrderKey":this.OrderKey,
      	}

      	this.httpRequest(currentUrl,postData,function(res){

      		that.clearData();
      		that.OrderKey = "";
      		that.$refs.OrderKey.focus();
      	},"OrderKey")

    },
      // 查看明细
    RF_OrderDetailQuery(){

      	var that = this;

      	if(!this.OrderKey){

      		this.msg = "请输入【发货单号】";
      		that.tipsShow("OrderKey");
      		that.inputFocus = "OrderKey";
      	}else{

      		if(!this.QtyTotal) {

      			this.RF_OrderQuery(false, false);
      		}else{


      			var currentUrl = this.HOST + "RFOrderDetailQuery.do";

      			var postData = {

      				"OrderKey":this.OrderKey,
      			}

      		  this.httpRequest(currentUrl,postData,function(res){


					that.$store.state.RFOrderShip_RFOrderDetailQuery = res.data.data;
  					that.$router.push({

  						name:"RFOrderDetailQuery",
  					})

      			},"OrderKey")
      		}
      	}
      },
    //返回上一页  首页
    toIndex:function(){

    	this.clearData();
      	this.$router.push({
      		name:"Index",
      	})
    },
  }
}
</script>
<style scoped lang="less">
.RFOrderShip{
	.container{
		&>div{
			width: 100%;
			height: 60/50rem;
			margin-top: 17/50rem;
			padding: 25/50rem 20/50rem;
			box-sizing: border-box;
			position: relative;

			span{
				width: 180/50rem;
				text-align: right;
				float: left;
			}

			input::placeholder{
				font-size: 25/50rem;
			}

			input{
				background-color: #f0f0f0;
				margin-left: 10/50rem;
				width: 470/50rem;
				height: 55/50rem;
				padding: 0 10/50rem;
				border:none;
				outline: none;
				box-sizing: border-box;
			}
			i{
				position:absolute;
				right:90/50rem;
				font-size: 37/50rem;
				top: 34/50rem;
			}
		}
		.address{

			padding-top: 17/50rem;
			&>textarea{

				width:450/50rem;
				height:70/50rem;
				padding:0 10/50rem;
				margin-left: 10/50rem;
				background-color: #f0f0f0;
				border:none;
				outline: none;
			}
		}
		&>div:nth-child(4),&>div:nth-child(5){
			margin-top: 25/50rem;
		}

		.writable{
			background-color: #fff;
			border:1/50rem solid #bbb;
		}
		input.writable:focus{ outline:#7BD039 solid 3/50rem; }
		
		.fuzhu{

			width:0;
			height:0;
			border:0;
		}
	}
}

// 374像素以下（包含374像素）
@media screen and (max-width: 374px){

	.RFOrderShip{
		.container{
			.writable{
				border:3/50rem solid #bbb;
			}
			&>div{
				i{
					top: 33/50rem;
				}
			}
		}
	}
}

// 255像素以下(包含255像素)
@media screen and (max-width: 255px){
	.RFOrderShip{
		.container{
			&>div{
				height: 80/50rem;
				i{
					right:80/50rem;
					top: 43/50rem;
				}
				span{
					width: 200/50rem;
				}
				input{
					width: 450/50rem;
				}
			}
			.address{
				&>textarea{
					width:431/50rem;
				}
			}
		}
	}
}

</style>


